.about {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
}

.hero {
  height: 90vh;
  padding-left: 10%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  line-height: 1.6;
  color: #333;
  max-width: 40%;
}

.hero_title {
  font-size: 3rem;
  padding-top: 5%;
  font-weight: 900;
}

.hero_accent {
  color: #2a9df4;
  font-weight: 900;
}

.hero_aboutme {
  font-weight: 500;
  justify-self: flex-start;
}
.hero_nav_social {
  padding-top: 5%;
}

.hero_nav_list {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  list-style-type: none;
}

.hero_button_social {
  background-color: transparent;
  border: 0;
  font-size: 3rem;
  color: #2a9df4;
}

.hero_photo {
  border-radius: 50%;
  max-width: 400px;
  max-height: 400px;
  margin-right: 10%;
}
