@media (max-width: 1024px) {
  .about {
    flex-direction: column-reverse;
    justify-content: center;
  }

  .hero {
    padding-left: 0px;
    max-width: 80%;
    justify-content: center;
  }

  .hero_photo {
    margin-top: 1%;
    border-radius: 50%;
    max-width: 400px;
    max-height: 400px;
    margin-right: 0px;
  }
}

@media (max-width: 512px) {
  .hero_photo {
    max-width: 200px;
    max-height: 200px;
  }
}
