.header {
  width: 100%;
  height: 10vh;
  background: #fff;
  top: 0;
  position: sticky;
}

.header_list {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  align-items: center;
  list-style-type: none;
  padding-right: 10%;
}

.header_item {
  padding: 0.5% 2%;
}

.header_link {
  color: #212121;
  text-decoration: none;
  text-transform: uppercase;
  font-weight: 400;
}

.header_link:hover {
  font-weight: 500;
}

.header_button {
  background: #2a9df4;
  padding: 10px 20px;
  border: 2px solid #2a9df4;
  border-radius: 23px;
}

.header_mail {
  color: #fff;
  font-size: 1.125rem;
  font-weight: 500;
  text-decoration: none;
}
