.container {
  padding: 2%;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  color: #444;
}

.skills_container {
  border-right: 3px solid#444;
}

.item::marker {
  color: #2a9df4;
}

.tools_container,
.skills_container {
  height: 100%;
  width: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}
